.membersContainer{
	position:relative;
	display: flex;
	justify-content:center;
	width:100%;
	
	align-items: center;
	flex-direction: column;
	&.isSelected{
		height:100vh;
		overflow: hidden;
	}
	.membersInnerContainer{
		position: absolute;
		top:180px;
		left:0;
		align-items:center;
		justify-content: center;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		.memberContainer{
			width:25%;
			margin:20px;
			.image{
				background:#f2f2f2;
				height:150px;
				width:150px;
				background-size: cover;
				background-position:center;
			}
		}
	
	}
}